import React, { useState, useEffect } from 'react';
import styles from "./Form.module.scss";
import Input from "../Input/Input"
import Title from '../Title/Title';
import Button from '../Button/Button';
import { io } from "socket.io-client";

const socket = io("http://localhost:8080/", {
    transports: ['websocket']
});

interface EventInterface {
    target: {
        value: string,
        name: string
    },
    onClick: (event: React.MouseEvent<HTMLButtonElement>) => void;
}




const Form = ({ setProducts }: any) => {
    const [data, setData] = useState({
        name: "",
        price: "",
        imgUrl: "",
        id: 0
    })

    socket.on('messages', (info) => {
        setProducts(info)
    })

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();

        socket.emit('new-message', data);
        const rawResponse = await fetch('http://localhost:8080/guardar', {
            method: 'POST',
            mode: 'cors',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
                'Access-Control-Allow-Origin': 'http://localhost:8080',
                'Access-Control-Allow-Credentials': 'true'
            },
            body: JSON.stringify(data)
        });
        const content = await rawResponse.json();
    };

    return (
        <>
            <Title hasMargin priority={3}>Agregar</Title>
            <form className={styles.Form} onSubmit={handleSubmit}
            >
                <div className={styles.InputContainer}>
                    <Input
                        name="message"
                        placeholder={"Remera"}
                        onChange={(e: EventInterface) => setData(state => ({ ...state, name: e.target.value }))} />
                    <Input
                        name="message"
                        placeholder={"200"}
                        onChange={(e: EventInterface) => setData(state => ({ ...state, price: e.target.value }))} />
                    <Input
                        name="message"
                        placeholder={"url imagen"}
                        onChange={(e: EventInterface) => setData(state => ({ ...state, imgUrl: e.target.value }))} />
                </div>
                <Button size={"10rem"}>
                    Agregar
                </Button>
            </form>


        </>
    )
}

export default Form;